import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import { Router } from 'react-router-dom';

ReactDOM.render(
  <React.StrictMode>
    <Router
      basename={process.env.PUBLIC_URL}
      // basename={"/react-rick-and-morty"}
      >
      <App />
    </Router>
  </React.StrictMode>,
  document.getElementById('root')
);